<template>
  <div>
    <div style="margin-top: 50px;height: 250px">
      <h3 class="title modal-headline"></h3>
      <div slot="tip" class="el-upload__tip">jpg/png/jpeg/gif/svg/bmp2MB</div>
      <el-upload
        class="upload-demo"
        accept=".png,.jpg,.jpeg,.gif,.svg,.bmp"
        ref="upload"
        :file-list="fileLists"
        :multiple="true"
        action="#"
        :on-change="handlePreview"
        :on-remove="handleRemove"
        list-type="text"
        :limit="100"
        :on-exceed="fileExceed"
        :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary"></el-button>
        <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">
        </el-button>
      </el-upload>
    </div>


    <div>
      <h2>{{imgInfos.length}}</h2>
      <h4>excelurl{{imgUrl}}+</h4>
      <el-table
        :data="imgInfos"
        border
        style="width: 50%;margin: auto">
        <el-table-column
          prop="index"
          label="index"
          width="110">
          <template slot-scope="scope">
            {{scope.$index}}
          </template>
        </el-table-column>
        <el-table-column
          prop="data"
          label="">
          <template slot-scope="scope">
            {{scope.row}}
          </template>
        </el-table-column>

        <el-table-column
          label=""
          width="110">
          <template slot-scope="scope">
            <el-button type="info" @click="del(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>

  export default {
    data() {
      return {
        fileLists: [],
        imgInfos: [],
        imgUrl: ''
      };
    },
    created() {
      this.convert();
    },
    methods: {
      convert() {
        this.$api.admin.getImg()
          .then(res => {
            this.imgInfos = res.imgList;
            this.imgUrl = res.imgUrl;
          }).catch(err => {
          this.$message.error('!');
        });
      },
      submitUpload() {
        let that = this;
        let params = new FormData();
        // 
        if (that.fileLists.length > 0) {
          that.fileLists.forEach(function (file) {
            params.append('questionImages', file.raw);
          });
          that.$api.admin.uploadImg(params)
            .then(res => {
              this.imgInfos = res.imgList;
              // 
              that.fileLists = [];
              this.$refs['upload'].clearFiles();
              that.$message.success('!');
            })
            .catch(err => {
              that.$message.error(err.response.data.message);
            });
        } else {
          that.$message.warning("");
        }

      },

      handleRemove(file, fileList) {
        this.fileLists = fileList;
      },

      handlePreview(file, fileList) {
        let that = this;
        if (file.raw.type != 'image/jpg' && file.raw.type != 'image/png'
          && file.raw.type != 'image/gif' && file.raw.type != 'image/svg'
          && file.raw.type != 'image/bmp' && file.raw.type != 'image/jpeg') {
          that.$message.error('jpg/png!');
          return;
        }
        if (file.raw.size > 1024 * 1024 * 2) {
          that.$message.error(' 2MB!');
          return;
        }
        that.fileLists = fileList;
      },

      fileExceed: function (files, fileList) {
        this.$message.warning(` 100  ${files.length}  ${files.length + fileList.length} `)
      },
      del(fileName) {
        let that = this;
        let params = new FormData();
        params.append('fileName', fileName);
        that.$api.admin.deleteImg(params)
          .then(res => {
            this.imgInfos = res.imgList;
            that.$message.success('!');
          })
          .catch(err => {
            that.$message.error(err.response.data.message);
          });
      },
    }
  }
</script>



